import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/home';
import Keyspace from '@/components/keyspace';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/keyspace',
      name: 'keyspace',
      component: Keyspace,
    },
  ],
});



