<template>
  <el-table
    :data="keyspace"
    stripe
    style="width: 100%">
    <el-table-column
      prop="durable_writes"
      label="durable_writes"
      width="180">
    </el-table-column>
    <el-table-column
      prop="keyspace_name"
      label="keyspace_name"
      width="180">
    </el-table-column>
    <el-table-column
      prop="replication.class"
      label="class">
    </el-table-column>
  </el-table>
</template>

<script>
import api from '@/api'

const service = api.make('root')

export default {
    name: 'Home',
    data() {
        return {
            keyspace: []
        }
    },
    created() {
        this.fetch()
    },
    methods: {
        async fetch() {
            try {
                const res = await service.request('allKeySpace')
                this.keyspace = res.get()
                console.error(res.get())
            } catch (error) {
                console.error('[error]', error)
            }
        }
    }
};


</script>


